"use client"

import { useState } from "react"
import { useSession } from "next-auth/react"
import { useRouter } from "next/navigation"
import { Milk, MapPin, Phone, User, ChevronRight } from "lucide-react"
import { Button, Input, Card } from "@/components/ui"
import toast from "react-hot-toast"

export default function CustomerRegisterPage() {
  const { data: session, update } = useSession()
  const router = useRouter()
  const [loading, setLoading] = useState(false)
  const [step, setStep] = useState(1)
  
  const [form, setForm] = useState({
    name: session?.user?.name || "",
    phone: "",
    addressLine1: "",
    addressLine2: "",
    landmark: "",
    city: "Pondicherry",
    pincode: "",
  })

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setForm({ ...form, [e.target.name]: e.target.value })
  }

  const handleSubmit = async () => {
    setLoading(true)

    try {
      const res = await fetch("/api/customer/register", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(form),
      })

      const data = await res.json()

      if (!res.ok) {
        throw new Error(data.error || "Registration failed")
      }

      toast.success("Registration successful! Awaiting admin approval.")
      await update() // Refresh session
      router.push("/customer")
      router.refresh()

    } catch (err: any) {
      toast.error(err.message)
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="min-h-screen bg-gradient-hero p-4">
      {/* Header */}
      <div className="text-center pt-8 pb-6">
        <div className="inline-flex items-center justify-center h-16 w-16 bg-primary-600 rounded-2xl mb-4">
          <Milk className="h-9 w-9 text-white" />
        </div>
        <h1 className="text-2xl font-bold text-neutral-900">Complete Your Profile</h1>
        <p className="text-neutral-600 mt-1">Just a few more details to get started</p>
      </div>

      {/* Progress */}
      <div className="flex items-center justify-center gap-2 mb-8">
        {[1, 2].map((s) => (
          <div
            key={s}
            className={`h-2 rounded-full transition-all ${
              s <= step ? "w-16 bg-primary-600" : "w-8 bg-neutral-200"
            }`}
          />
        ))}
      </div>

      {/* Form */}
      <Card variant="elevated" className="max-w-md mx-auto">
        {step === 1 && (
          <div className="space-y-4 animate-fadeIn">
            <h2 className="text-lg font-semibold text-neutral-900 mb-4">
              Personal Details
            </h2>
            
            <Input
              label="Full Name"
              name="name"
              value={form.name}
              onChange={handleChange}
              placeholder="Enter your full name"
              leftIcon={<User className="h-5 w-5" />}
            />

            <Input
              label="Phone Number"
              name="phone"
              type="tel"
              value={form.phone}
              onChange={handleChange}
              placeholder="10-digit mobile number"
              leftIcon={<Phone className="h-5 w-5" />}
              hint="We'll use this to contact you about deliveries"
            />

            <Button
              onClick={() => {
                if (!form.name || !form.phone) {
                  toast.error("Please fill in all fields")
                  return
                }
                if (!/^[6-9]\d{9}$/.test(form.phone)) {
                  toast.error("Please enter a valid 10-digit phone number")
                  return
                }
                setStep(2)
              }}
              fullWidth
              size="lg"
              className="mt-6"
            >
              Continue
              <ChevronRight className="h-5 w-5" />
            </Button>
          </div>
        )}

        {step === 2 && (
          <div className="space-y-4 animate-fadeIn">
            <h2 className="text-lg font-semibold text-neutral-900 mb-4">
              Delivery Address
            </h2>

            <Input
              label="Address Line 1"
              name="addressLine1"
              value={form.addressLine1}
              onChange={handleChange}
              placeholder="House/Flat No., Building Name"
              leftIcon={<MapPin className="h-5 w-5" />}
            />

            <Input
              label="Address Line 2 (Optional)"
              name="addressLine2"
              value={form.addressLine2}
              onChange={handleChange}
              placeholder="Street, Area"
            />

            <Input
              label="Landmark (Optional)"
              name="landmark"
              value={form.landmark}
              onChange={handleChange}
              placeholder="Near temple, school, etc."
            />

            <div className="grid grid-cols-2 gap-4">
              <Input
                label="City"
                name="city"
                value={form.city}
                onChange={handleChange}
                placeholder="City"
              />

              <Input
                label="Pincode"
                name="pincode"
                value={form.pincode}
                onChange={handleChange}
                placeholder="6-digit pincode"
                maxLength={6}
              />
            </div>

            <div className="flex gap-3 mt-6">
              <Button
                variant="outline"
                onClick={() => setStep(1)}
                className="flex-1"
              >
                Back
              </Button>
              <Button
                onClick={handleSubmit}
                loading={loading}
                className="flex-1"
              >
                Submit
              </Button>
            </div>
          </div>
        )}
      </Card>

      {/* Info */}
      <div className="max-w-md mx-auto mt-6 p-4 bg-primary-50 rounded-xl">
        <p className="text-sm text-primary-800">
          <strong>What happens next?</strong><br />
          Our team will review your details and assign a delivery person for your area. 
          You'll be notified once approved.
        </p>
      </div>
    </div>
  )
}
